import React from "react";
import { Button } from "@material-ui/core";

export const getColumns = (handleRemoveEmployee, handleRemoveAllEmployee) => [
    {
      minWidth: 178,
      resizable: true,
      Header: "–ò–º—è",
      accessor: "firstName"
    },
    {
      minWidth: 177,
      resizable: true,
      Header: "–§–∞–º–∏–ª–∏—è",
      accessor: "lastName"
    },
    {
      minWidth: 177,
      resizable: true,
      Header: "–í–æ–∑—Ä–∞—Å—Ç",
      accessor: "age"
    },
    {
      id: "delete",
      accessor: () => "delete",
      width: 130,
      Header: (tableProps) => {
        const { isAllRowsSelected } = tableProps;
        if (isAllRowsSelected) {
          return (
            <Button
              size="small"
              variant="contained"
              color="secondary"
              onClick={() => {
                handleRemoveAllEmployee()
              }}
            >
              –£–¥–∞–ª–∏—Ç—å –≤—Å—ë
            </Button>
          );
        }
        return null;
      },
      Cell: (tableProps) => (
        <Button
          size="small"
          variant="contained"
          color="secondary"
          onClick={() => {
            const { index: rowIndex } = tableProps?.row;
            const dataId = tableProps?.data[rowIndex]?.id;
            const { rows } = tableProps;
            const selectedRows = rows
              .filter((row) => {
                const isSelected = row.isSelected;
                if (isSelected) return row.original.id;
                return false;
              })
              .map((row) => row.original.id);
            if (dataId) {
              handleRemoveEmployee(dataId, rowIndex, selectedRows)
            }
          }}
        >
          –£–¥–∞–ª–∏—Ç—å
        </Button>
      )
    }
  ]